angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/bill/bill.html","<button class=\"btn bill__button\" ng-style=\"$ctrl.bgcolor\" ng-click=\"$ctrl.addAmount(1)\">{{ $ctrl.value }}</button>\n<span class=\"bill__times\">&times;</span>\n<input type=\"number\" min=\"0\" ng-change=\"$ctrl.amountChanged(amount)\" ng-model=\"$ctrl.amount\">\n<span class=\"bill__equals\">&equals;</span>\n<span class=\"bill__sum\">{{ $ctrl.sum | number }}</span>\n");
$templateCache.put("app/counting/counting.html","<header>\n  <h1>Conta Grana</h1>\n  <h2>Conte seu dinheiro com facilidade!</h2>\n</header>\n<main>\n  <div class=\"counting__bills\">\n    <bill ng-repeat=\"bill in $ctrl.bills\" value=\"{{ bill.value }}\" color=\"{{ bill.color }}\" on-amount-changed=\"$ctrl.updateAmount($index, amount)\">\n  </div>\n  <button ng-click=\"reset()\" class=\"btn btn-sm btn-default counting__reset\">Limpar</button>\n  <span class=\"counting__sum\">\n    R$ {{ $ctrl.sum | number:2 }}\n  </span>\n</main>\n<footer>\n  Conta Grana - Conte seu dinheiro com facilidade!\n  <small>\n    Por <span class=\"author\">Rodrigo Waltenberg</span>\n  </small>\n</footer>\n");}]);